import { object_from_querystring, querystring_from_object } from "universal-common-scripts/functions/urls"

/*
  1) CONFIG - DEFAULT STATE:
*/
const initialState = {
  get_domains: false,
  input_spellchecked: "",
  input_first_word: "",
  input_str: "",
  input_tld: "",
  input_captcha2: "",
  input_captcha3: "",
  suggestions_options: {
    length_vs_relevance: 5,
    ext_suggestions_num: 30,
    group_by: "both",
    use_domain_hacks: true,
    use_word_hacks: true,
    use_synonyms: true,
    use_before_after: true,
    return_word_rows: true,
    group_by_type: true,
    group_by_availability: true
  }, // dump {key:value} options into here - will be passed to search API
  in_progress: false,
  params_stringified: "",
  search_now: 0,
  auth_expires: 10000000000000,
  only_available: true
}

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = initialState, action) => {
  let newState = { ...state }

  /*
    3) HANDLE ACTIONS:
  */
  switch (action.type) {
    /*
     * show unavailable results
     */
    case "RX__toggle_only_available":
      newState.only_available = !newState.only_available
      break
    /*
     * trigger search when user interacts with the Search.js component
     */
    case "RX__search_now":
      newState.search_now++
      break

    /*
     * inputs must all be set at the same time, to avoid multiple re-renders:
     */
    case "RX__set_inputs":
      {
        /*
         * set each input
         */
        for (let key in action.data) {
          newState["input_" + key] = action.data[key] || initialState["input_" + key]
        }
      }
      break

    case "RX__clear_inputs":
      {
        // reset state
        newState.input_tld = initialState.input_tld
        newState.input_str = initialState.input_str
        newState.input_captcha2 = initialState.input_captcha2
        newState.input_captcha3 = initialState.input_captcha3
        newState.input_spellchecked = initialState.input_spellchecked
        newState.input_first_word = initialState.input_first_word
      }
      break

    case "RX__set_suggestions_options":
      if (action.data) {
        for (let key in action.data) {
          newState.suggestions_options[key] = action.data[key]
        }
      } else {
        newState.suggestions_options = initialState.suggestions_options
      }
      break

    case "RX__in_progress":
      newState.in_progress = !!action.data
      break

    case "RX__reset":
      newState = { ...initialState }
      break

    case "RX__params_stringified":
      newState.params_stringified = action.data
      break

    case "RX__auth_expires":
      newState.auth_expires = action.data
      break
  }
  /*
    4) RETURN (COPY OF) STATE:
  */
  // console.log(
  //   [action.type, action],
  //   JSON.parse(JSON.stringify(newState)),
  // )
  return newState
}

export default ThisReducer
